
import {http} from './Common';



//   config  historyPopCache() merge   


export const authLogOutAPI = {
  get: ({ page, config, history } = {}) => {
    return http.get(`/auth/logout`)
  }
}

export const authSocialLoginAPI = {
  get: ({ page, config, history} = {}) => {
    return http.get(`/auth/social/${page}`);
  }
}